.bannerParent {
    background-image: url("../assets/canadaBlue.jpeg");
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 30px 0px;
    padding-top: 150px;
  }
  .banner {
    width: 1440px;
    margin: auto;
    text-align: center;
  }
  
  .bannerHeading h1 {
    color: #e8c47c;
    font-size: 50px;
  }
  
  .bannerHeading p {
    color: #ffffff;
    width: 74%;
    margin: auto;
    font-size: 25px;
    line-height: 1.8;
    padding-top: 60px;
  }
  
  .bannerHeadingRotateParent {
    width: 700px;
    margin: auto;
    display: flex;
    text-align: center;
    justify-content: center;
    /* gap: 10px; */
    align-items: center;
    color: white;
    /* margin-top: 150px; */
  }
  
  .bannerHeadingRotate {
    transform: rotate(-90deg);
    border-bottom: 1px solid white;
    padding-bottom: 20px;
    font-size: 25px;
  }
  
  .bannerHeadingRotatePara p {
    text-align: left;
    padding-bottom: 8px;
    font-size: 28px;
  }
  

  .bannerHeadingRotatePara p {
    cursor: pointer;
  }
    
    @media (max-width: 1450px) {
      .banner {
        width: 95%;
      }
    }
    
    @media (max-width: 1080px) {
      .bannerHeadingRotateParent {
        flex-direction: column;
        text-align: center;
        /* margin-top: 50px; */
        position: relative;
        width: 100%;
      }
    
      .bannerHeadingRotate {
        transform: none;
        border: none;
        padding-bottom: 0;
        font-size: 24px;
        text-align: left;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
      }
    
      .bannerHeadingRotate::after {
        content: "\25BC";
        font-size: 16px;
        margin-left: 10px;
        transition: transform 0.3s;
      }
    
      .bannerHeadingRotateParent.active .bannerHeadingRotate::after {
        transform: rotate(180deg);
      }
    
      .bannerHeadingRotatePara {
        margin-left: 0;
        display: none;
        background-color: #f7f7f7;
        padding: 10px;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 10;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        padding-top: 25px 0px;
      }
    
      .bannerHeadingRotateParent.active .bannerHeadingRotatePara {
        display: block;
      }
    
      .bannerHeadingRotatePara p {
        font-size: 18px;
        padding: 10px 0;
        color: #00273f;
        align-items: center;
        text-align: center;
      }
    }
  
  
  /* Cby.module.css */
  .container {
      max-width: 1440px;
      margin: 0 auto;
      padding: 20px;
      text-align: left;
    }
    
    .heading {
      font-size: 2.1rem;
      font-weight: bold;
      margin-bottom: 20px;
      text-align: left;
    }
    
    .introduction, .benefits, .eligibility, .applicationProcess, .refusalReasons, .assistance, .whyChooseUs {
      margin-bottom: 20px;
    }
    .applicationProcess li{
      margin-top: 15px;
    }

    .refusalReasons ul{
      margin-left: 40px !important;
    }
  
    .eligibility p{
      margin-bottom: 20px;
    }
  
    .eligibility h3, h4{
      margin-bottom: 20px;
    }
  
    .introduction p{
      text-align: center;
      margin-bottom: 20px;
    }
    
    .subheading {
      /* font-size: 1.2rem; */
      margin-bottom: 15px;
      color: #333;
      text-align: left;
    }
    
  
    .button {
      display: inline-block;
      background-color: #00273f;
      color: white;
      padding: 10px 20px;
      text-align: center;
      text-decoration: none;
      border-radius: 5px;
      font-size: 1rem;
      margin-top: 20px;
      border: none;
      cursor: pointer;
      display: block;
      margin: auto;
    }
    
    .button:hover {
      opacity: 0.9;
      text-decoration: none;
    }
    
  
    
    /* Table styling */
    .table {
      width: 100%;
      border-collapse: collapse;
      margin-top: 20px;
    }
  
    .table ul{
      list-style: none;
    }
    
    .table th, .table td {
      border: 1px solid #00273f;
      padding: 10px;
      text-align: left;
    }
    
    .table th {
      background-color: #f4f4f4;
      font-weight: bold;
      color: #00273f;
    }
    
    .table ul {
      margin: 0;
      padding: 0;
      /* list-style-type: none; */
    }
    
    .table ul li {
      margin-bottom: 5px;
    }
  
    
    
    .container h2 {
      color: #00273f;
      margin-top: 30px;
      margin-bottom: 20px;
      text-align: left;
    }
    
    .heading {
      color: #e8c47c;
      margin-top: 20px;
      margin-bottom: 20px;
      text-align: center;
    }
  
    .table{
      margin-top: 30px;
      margin-bottom: 20px;
    }
    .section {
      opacity: 0;
      transform: translateX(-100px);
      transition: opacity 0.8s ease, transform 0.8s ease;
      margin-top: 50px;
      margin-bottom: 50px;
    }
    
    .section.visible {
      opacity: 1;
      transform: translateX(0);
    }
    
    .section button {
      display: block;
      margin: auto;
      background-color: #00273f;
      color: white;
      padding: 15px;
      border-radius: 3px;
      margin-top: 20px;
      text-decoration: none;
      cursor: pointer;
      border: none;
    }
    .section button:hover {
      opacity: 0.9;
      text-decoration: none;
    }
  
    @media (max-width: 1080px) {
      .container h2 {
        font-size: 20px;
        margin-top: 30px;
        margin-bottom: 10px;
      }
    
      .heading {
        font-size: 26px;
      }
  
      .eligibility h3,h4{
        font-size: 18px;
        
      }
    }
    .whychooseusLi li{
      margin-top: 8px;
    }