.bannerParent {
  background-image: url("../assets/canadaBlue.jpeg");
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 30px 0px;
  padding-top: 150px;
}
.banner {
  width: 1440px;
  margin: auto;
  text-align: center;
}

.bannerHeading h1 {
  color: #e8c47c;
  font-size: 50px;
}

.bannerHeading p {
  color: #ffffff;
  width: 74%;
  margin: auto;
  font-size: 25px;
  line-height: 1.8;
  padding-top: 60px;
}

.bannerHeadingRotateParent {
  width: 700px;
  margin: auto;
  display: flex;
  text-align: center;
  justify-content: center;
  /* gap: 10px; */
  align-items: center;
  color: white;
  /* margin-top: 150px; */
}

.bannerHeadingRotate {
  transform: rotate(-90deg);
  border-bottom: 1px solid white;
  padding-bottom: 20px;
  font-size: 25px;
}

.bannerHeadingRotatePara p {
  text-align: left;
  padding-bottom: 8px;
  font-size: 28px;
}

.bannerHeadingRotatePara p {
  cursor: pointer;
}

@media (max-width: 1450px) {
  .banner {
    width: 95%;
  }
}

@media (max-width: 1080px) {
  .bannerHeadingRotateParent {
    flex-direction: column;
    text-align: center;
    /* margin-top: 50px; */
    position: relative;
    width: 100%;
  }

  .bannerHeadingRotate {
    transform: none;
    border: none;
    padding-bottom: 0;
    font-size: 24px;
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
  }

  .bannerHeadingRotate::after {
    content: "\25BC";
    font-size: 16px;
    margin-left: 10px;
    transition: transform 0.3s;
  }

  .bannerHeadingRotateParent.active .bannerHeadingRotate::after {
    transform: rotate(180deg);
  }

  .bannerHeadingRotatePara {
    margin-left: 0;
    display: none;
    background-color: #f7f7f7;
    padding: 10px;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    z-index: 10;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding-top: 25px 0px;
  }

  .bannerHeadingRotateParent.active .bannerHeadingRotatePara {
    display: block;
  }

  .bannerHeadingRotatePara p {
    font-size: 18px;
    padding: 10px 0;
    color: #00273f;
    align-items: center;
    text-align: center;
  }
}

/* General container styles */
.container {
  padding: 20px;
  max-width: 1440px;
  margin: 0 auto;
  text-align: left;
}

/* Title and subtitle styles */
.title {
  font-size: 2rem;
  font-weight: bold;
  color: #e8c47c;
  margin-bottom: 30px;
  margin-top: 30px;
}
/* .container ul{
    list-style-type: none;
  } */

.subtitle {
  font-size: 1.5rem;
  font-weight: bold;
  color: #00273f; /* Dark blue color */
  margin-top: 20px;
  margin-bottom: 20px;
}

.subtitle h2 {
  margin-top: 20px;
  margin-bottom: 30px;
}

.subheading {
  font-size: 1.2rem;
  font-weight: bold;
  color: #00273f; /* Slightly lighter blue */
  margin-top: 15px;
  margin-bottom: 5px;
}

/* Paragraph and list styles */
.description {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 20px;
}

.list {
  list-style-type: disc;
  margin-left: 20px;
}

.listItem {
  margin-bottom: 10px;
}

.nestedList {
  list-style-type: circle;
  margin-left: 20px;
}

.listItem ol {
  list-style-type: decimal;
}

.nestedListItem {
  margin-bottom: 5px;
}

/* Button styles */
.button {
  display: inline-block;
  padding: 10px 20px;
  margin: 10px 0;
  background-color: #00273f; /* Dark blue */
  color: #fff;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  margin-right: 30px;
  display: block;
  margin: auto;
  border: none;
  cursor: pointer;
  display: block;
  margin: auto;
  margin-top: 30px;
}

.button:hover {
  opacity: 0.9;
  text-decoration: none;
}

.subheading h3 {
  margin-top: 20px;
  margin-bottom: 30px;
  text-align: center;
}

.container h2 {
  color: #00273f;
  margin-top: 30px;
  margin-bottom: 20px;
  text-align: left;
}

.title {
  color: #e8c47c;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
}

.section {
  opacity: 0;
  transform: translateX(-100px);
  transition: opacity 0.8s ease, transform 0.8s ease;
  margin-top: 30px;
  /* margin-bottom: 50px; */
}

.section.visible {
  opacity: 1;
  transform: translateX(0);
}

@media (max-width: 400px) {
  .container h2 {
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 10px;
  }

  .header h1 {
    font-size: 26px;
  }
}
