
.bannerParent {
    background-image: url("../assets/canadaBlue.jpeg");
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 30px 0px;
    padding-top: 150px;
    margin-bottom: 50px;
  }
  .banner {
    width: 1440px;
    margin: auto;
    text-align: center;
  }
  
  .bannerHeading h1 {
    color: #e8c47c;
    font-size: 50px;
  }
  
  .bannerHeading p {
    color: #ffffff;
    width: 74%;
    margin: auto;
    font-size: 25px;
    line-height: 1.8;
    padding-top: 60px;
  }
  
  .bannerHeadingRotateParent {
    width: 700px;
    margin: auto;
    display: flex;
    text-align: center;
    justify-content: center;
    /* gap: 10px; */
    align-items: center;
    color: white;
    /* margin-top: 150px; */
  }
  
  .bannerHeadingRotate {
    transform: rotate(-90deg);
    border-bottom: 1px solid white;
    padding-bottom: 20px;
    font-size: 25px;
  }
  
  .bannerHeadingRotatePara p {
    text-align: left;
    padding-bottom: 8px;
    font-size: 28px;
  }
  
  
  .bannerHeadingRotatePara p {
    cursor: pointer;
  }
    
    @media (max-width: 1450px) {
      .banner {
        width: 95%;
      }
    }
    
    @media (max-width: 1080px) {
      .bannerHeadingRotateParent {
        flex-direction: column;
        text-align: center;
        /* margin-top: 50px; */
        position: relative;
        width: 100%;
      }
    
      .bannerHeadingRotate {
        transform: none;
        border: none;
        padding-bottom: 0;
        font-size: 24px;
        text-align: left;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
      }
    
      .bannerHeadingRotate::after {
        content: "\25BC";
        font-size: 16px;
        margin-left: 10px;
        transition: transform 0.3s;
      }
    
      .bannerHeadingRotateParent.active .bannerHeadingRotate::after {
        transform: rotate(180deg);
      }
    
      .bannerHeadingRotatePara {
        margin-left: 0;
        display: none;
        background-color: #f7f7f7;
        padding: 10px;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 10;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        padding-top: 25px 0px;
      }
    
      .bannerHeadingRotateParent.active .bannerHeadingRotatePara {
        display: block;
      }
    
      .bannerHeadingRotatePara p {
        font-size: 18px;
        padding: 10px 0;
        color: #00273f;
        align-items: center;
        text-align: center;
      }
    }
    
  
  
  .container {
      max-width: 1440px;
      margin: 0 auto;
      padding: 20px;
      box-sizing: border-box;
      text-align: left;
    }
    
    .header {
      text-align: left;
      margin-bottom: 20px;
    }
    
    .header h1 {
      font-size: 2.5em;
      color: #00273f;
    }
    
    .mainContent {
      line-height: 1.6;
    }
    
    .section {
      margin-bottom: 50px;
    }
    
    .section h2 {
      font-size: 2em;
      color: #00273f;
    }
    
   .section h3 {
      font-size: 1.5em;
      color: #00273f;
    }
    
    .section p {
      font-size: 1em;
      color: #666;
    }
    .section li{
      font-size: 1em;
      color: #5a5454;
      margin-top: 10px; 
      /* list-style-type: none; */
    }
    
    .categories {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
    }
    
    .section .button {
      display: inline-block;
      padding: 10px 20px;
      margin: 5px;
      text-align: center;
      text-decoration: none;
      color: #fff;
      background-color: #00273f;
      border-radius: 5px;
    }
    
    .section .button:hover {
     opacity: 0.9;
    }
  
  
    .bannerHeadingRotatePara p{
      color: white;
    }
    .bannerHeadingRotate h3{
      color: white;
    }
  
  
    .container h2 {
      color: #00273f;
      margin-top: 20px;
      margin-bottom: 20px;
      text-align: left;
    }
    
    .header h1{
      color: #e8c47c;
      margin-top: 20px;
      margin-bottom: 20px;
      text-align: center;
    }
    .header p{
      text-align: center;
    }
  
    
  .section {
    opacity: 0;
    transform: translateX(-100px);
    transition: opacity 0.8s ease, transform 0.8s ease;
  }
  
  .section.visible {
    opacity: 1;
    transform: translateX(0);
  }
  .section button {
    display: block;
    margin: auto;
    background-color: #00273f;
    color: white;
    padding: 15px;
    border-radius: 3px;
    margin-top: 20px;
    text-decoration: none;
    cursor: pointer;
    border: none;
  }
  .section button:hover {
    opacity: 0.9;
    text-decoration: none;
  }
  
    @media (max-width: 400px) {
      .container h2 {
        font-size: 20px;
      }
    
      .header h1{
        font-size: 26px;
      }
    }
    
    .whychooseusLi li{
      margin-top: 8px;
    }