.section {
  opacity: 0;
  transform: translateX(-100px);
  transition: opacity 0.8s ease, transform 0.8s ease;
  margin-bottom: 50px;
}

.section.visible {
  opacity: 1;
  transform: translateX(0);
}

.section button {
  display: block;
  margin: auto;
  background-color: #00273f;
  color: white;
  padding: 15px;
  border-radius: 3px;
  margin-top: 20px;
  text-decoration: none;
  cursor: pointer;
  border: none;
}
.section button:hover {
  opacity: 0.9;
  text-decoration: none;
}
